$font-family: 'Arial', sans-serif;
$font-size: 20px;
$color-primary: #2F4F4F;
$white: #FFF;
$rank-color: #8B0000;

body {
    margin: 0;
    font-family: $font-family;
    color: $color-primary;
    font-size: $font-size;
}

.people {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(2, 200px);
    justify-content: center;
    gap: 0 30px;
    padding-top: 30px;
    padding-bottom: 155px;

    @media (min-width: 1000px) {
        grid-template-columns: repeat(4, 220px);
    }

    .person{
       display: flex;
       flex-direction: column;
       align-items: center;
       padding-top: 10px;

       &__photo{
        display: flex;
        position: relative;
        justify-content: right;
        align-items: center;

        &-img{
            width: 200px;
            height: 200px;
            border-radius: 50%;
            transition: transform .3s linear;
            transition-delay: 100ms;
        }

        &-rank{
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            width: 32px;
            height: 32px;
            background-color: $white;
            border-radius: 50%;
            right: 10px;
            color: $rank-color;
            line-height: 18.4px;
            font-size: 16px;
        }
        }

        &:hover {
            cursor: pointer;
            background-color: #EEE;
            border-radius: 10px;
        }

        &:hover .details__extra{
            transform: scale(1);
        }

        &:hover .person__photo-img{
            transform: scale(1.05);
            border-radius: 20%;
        }

    }

    .details{
        text-align: center;
        margin-top: 8px;

        &__extra{
            font-size: 18px;
            line-height: 20.7px;
            color: #000;
            transform: scale(0);
            margin-top: 9px;
            padding-bottom: 16px;
        }
    }

}
